import React from 'react';
import PropTypes from 'prop-types';
import { translate, getOptionSet, getOptionList } from '../Translator';

/*function getOptionsX (name) {
    const set = getOptionSet(name);
    const opts = [];
    Object.getOwnPropertyNames(set).forEach( key => {
    // Object.keys(set).forEach( key => {
        opts.push( <option key={key} value={key}>{set[key]}</option> );
    });
    return opts;
}*/

function getOptions (name) {
    const list = getOptionList(name);
    const opts = [];
    list.forEach( row => {
        opts.push( <option key={row.id} value={row.id}>{row.text}</option> );
    });
    return opts;
}

function checkValue(name,val) {
    const set = getOptionSet(name);
    return !!set[val] ? val : '';
}

const CLS_COMP = 'select-container';
const CLS_VALID = 'select-container valid';
const CLS_INVALID = 'select-container invalid';

export default class Select extends React.Component {

    static propTypes = {
        ariaDescribedBy: PropTypes.string,
        dateFormat: PropTypes.string,
        id: PropTypes.string,
        invalid: PropTypes.bool,
        onBlur: PropTypes.func,
        onChange: PropTypes.func,
        onFocus: PropTypes.func,
        onStateChange: PropTypes.func,
        options: PropTypes.string,          // ie 'country'
        required: PropTypes.bool,
        value: PropTypes.string
    };

    static validateInput (props) {
        const rendervalue = checkValue('country',props.value);
        if (!rendervalue) {
            return props.id + '-error';
        }
    }

    onBlur = () => {
        if (!this.props.touch) this.props.onStateChange( { touch: true } );
    };

    onChange = (ev) => {
        this.props.onStateChange( { value: ev.target.value, touch: true } );
    };

    render () {
        const { id, value = '', required, options, ariaDescribedBy, invalid, onBlur, onChange, onFocus, placeholder } = this.props;
        const rendervalue = checkValue(options,value);
        const cnm = rendervalue!=='' ? 'input' : 'input empty';
        const please = !rendervalue;
        const pleaseSelect = translate( placeholder ? placeholder : id+'-please-select');
        const cls = invalid ? CLS_INVALID : value ? CLS_VALID : CLS_COMP;
        return (
            <div className={cls}>
            <select
                aria-describedby={ariaDescribedBy}
                aria-invalid={invalid}
                className={cnm}
                id={id}
                onBlur={onBlur || this.onBlur}
                onChange={onChange || this.onChange}
                onFocus={onFocus}
                required={required}
                value={rendervalue}
            >
                {please && <option disabled={required} value="">{pleaseSelect}</option>}
                {getOptions(options)}
            </select>
            </div>
        );
    }

}

/* Notes
    select-container is only present so the select can be styled
 */


