import React from 'react';
import {translate, getOptionList} from "../Translator";

export default function Radios ({ id, value, options, label, onStateChange }) {

    function onChange (ev) {
        onStateChange( { value: ev.target.value } );
    }

    function renderRadios () {
        const list = getOptionList(options);
        const opts = [];
        list.forEach( row => {
            const key = row.id;
            const full = id+'-'+key;
            opts.push(
                <span key={full}>
                    <input id={full} name={id} type="radio" value={key} checked={key===value} onChange={onChange} />
                    <label htmlFor={full}>
                        {row.text}
                    </label>
                </span>
            );
        });
        return opts;
    }

    /*function renderRadiosXX () {
        const set = getOptionSet(options);
        const opts = [];
        Object.keys(set).forEach( key => {
            const full = id+'-'+key;
            opts.push(
                <span key={full}>
                    <label htmlFor={full}>
                        <input id={full} name={id} type="radio" value={key} checked={key===value} onChange={onChange} />
                        {set[key]}
                    </label>
                </span>
            );
        });
        return opts;
    }*/

    const labelText = translate(label);
    return (
        <fieldset aria-label={labelText} className="radio-fieldset" id={id}>
            <legend>{labelText}</legend>
            {renderRadios()}
        </fieldset>
    );
}



