import React from 'react';

export const ShowErrorsContext = React.createContext(false);

export default function UltraSection ({ showErrors, children }) {
    return (
        <ShowErrorsContext.Provider value={showErrors}>
            <div>
                {children}
            </div>
        </ShowErrorsContext.Provider>
    );
}



