import React from 'react';
import PropTypes from 'prop-types';

const CLS_COMP = 'inputText';
const CLS_VALID = 'inputText valid';
const CLS_INVALID = 'inputText invalid';

export default class Input extends React.Component {

    static propTypes = {
        ariaDescribedBy: PropTypes.string,
        dateFormat: PropTypes.string,
        id: PropTypes.string,
        invalid: PropTypes.bool,
        maxLength: PropTypes.number,
        onBlur: PropTypes.func,
        onChange: PropTypes.func,
        onFocus: PropTypes.func,
        onStateChange: PropTypes.func,
        required: PropTypes.bool,
        type: PropTypes.string,
        value: PropTypes.string
    };

    onBlur = () => {
        if (!this.props.touch) this.props.onStateChange( { touch: true } );
    };

    onChange = (ev) => {
        this.props.onStateChange( { value: ev.target.value } );
    };

    render() {
        const { id, value, type = 'text', maxLength, required, ariaDescribedBy, invalid, onBlur, onChange, onFocus } = this.props;
        const cls = invalid ? CLS_INVALID : value ? CLS_VALID : CLS_COMP;
        return (
            <input
                aria-describedby={ariaDescribedBy}
                aria-invalid={invalid}
                className={cls}
                id={id}
                maxLength={maxLength}
                onBlur={onBlur || this.onBlur}
                onChange={onChange || this.onChange}
                onFocus={onFocus}
                required={required}
                type={type}
                value={value}
            />
        );
    }
}

/*
Notes
 ariaDescribedBy can be a list of ids, "label1 label2 label3"

 minLength
 alpha-numeric + space
 */



